#dice{
    display: flex;
    align-items: center;
    justify-content: center;
    transform-style: preserve-3d;
    transition: transform 2s ease-out;
    cursor:pointer;
}
.side{
    position: absolute;
    width:0;
    height:0;
    transform-origin: 50% 66.6%;
    z-index:1;
    background-image: url("../../triangle.png");
    background-size: 100% 100% ;
}
#text{  
    display: flex;
    align-items: center;
    justify-content: center;

    z-index:1;
    opacity: 0%;
    transition: opacity 0.5s linear 2s;
    font-weight:bold;
    color:white;
    background-size: 100% auto;
    font-family:"EffraMedium";
}
#slide{
    margin-top: 30px;
    position: absolute;
    height:150px;
    overflow:hidden;
    max-width:0;
    transition: max-width 2s ease-out;
}
#tabletop{
    height:150px;
    
    width:800px;
}
#anim-container{
    width:1000px;
}